import { Component, OnInit,Renderer2, ElementRef } from '@angular/core';
import { MatTableDataSource } from '@angular/material';
import {FormControl} from '@angular/forms';
import {Observable} from 'rxjs';
import {map, startWith} from 'rxjs/operators';


export interface PeriodicElement {
  name: string;
  position: number;
  price: string;
  size: string;
  amount: number;
}
const ELEMENT_DATA: PeriodicElement[] = [
{position: 1, name: 'Straight Cut Fries-R', price: '125', size: 'Regular', amount: 0},
{position: 2, name: 'Straight Cut Fries-M', price: '175', size: 'Medium', amount: 0},
{position: 3, name: 'Straight Cut Fries-L', price: '125', size: 'Regular', amount: 0},
{position: 4, name: 'Tangled Tango', price: '0', size: '',amount: 0},
{position: 5, name: 'Eldorado Buzz', price: '0', size: '',amount: 0},
{position: 6, name: 'Cloud Green', price: '0', size: '',amount: 0},
{position: 7, name: 'Winky Twister', price: '0', size: '',amount: 0},
{position: 8, name: 'Mayo-Li', price: '0', size: '',amount: 0},


];

@Component({
  selector: 'app-bill-items',
  templateUrl: './bill-items.component.html',
  styleUrls: ['./bill-items.component.scss']
})
export class BillItemsComponent implements OnInit {
  displayedColumns: string[] = ['position', 'name', 'email' , 'amount'];
  dataSource: MatTableDataSource<any>;
  myControl = new FormControl();
  options: string[] = ['Straight Cut Fries-R', 'Straight Cut Fries-M', 'Straight Cut Fries-L', 
                        'Tangled Tango', 'Eldorado Buzz', 'Cloud Green','Winky Twister','Mayo-Li' ];
  filteredOptions: Observable<string[]>;
  selectedtableData: PeriodicElement[] = [];

  constructor(private renderer: Renderer2, private el: ElementRef) { }

  ngOnInit() {
//     const div = this.renderer.createElement('div');
//     const hi = this.renderer.createElement('h1');
// const text = this.renderer.createText('hello')
//   this.renderer.appendChild(div, hi);
//   this.renderer.appendChild(hi, text);
//   this.renderer.appendChild(this.el.nativeElement, div);
this.filteredOptions = this.myControl.valueChanges
      .pipe(
        startWith(''),
        map(value => this._filter(value))
      );
  }

  selectedOption(event) {
    const selectedValue = event.option.value;
    let selectedvalueArr: PeriodicElement = ELEMENT_DATA.find(e=>e.name==selectedValue);
    
    selectedvalueArr && this.selectedtableData.push(selectedvalueArr)
     this.dataSource = new MatTableDataSource(this.selectedtableData);
     console.log(selectedvalueArr);
     const div = this.renderer.createElement('div');
     this.renderer.setAttribute(div, 'class', 'single-item');
    const pOne = this.renderer.createElement('p');
    this.renderer.setAttribute(pOne, 'fxFlex', '30%');
    const pTwo = this.renderer.createElement('p');
    this.renderer.setAttribute(pTwo, 'fxFlex', '30%');
    const pTh = this.renderer.createElement('p');
    const qty = this.renderer.createElement('input');
    this.renderer.setAttribute(qty, 'type', 'number');
    this.renderer.setProperty(qty, '(ngModel)', 'number');
    // this.renderer.appendChild(pTh, qty);  
const itemName = this.renderer.createText(selectedvalueArr.name);
const itemPrice = this.renderer.createText(selectedvalueArr.price);
  this.renderer.appendChild(div, pOne);
  this.renderer.appendChild(pOne, itemName);
  this.renderer.appendChild(div, pTwo);
  this.renderer.appendChild(pTwo, itemPrice);
  this.renderer.appendChild(div, pTh);
  this.renderer.appendChild(pTh, qty);
  this.renderer.appendChild(this.el.nativeElement, div);
 }

  private _filter(value: string): string[] {
    const filterValue = value.toLowerCase();

    return this.options.filter(option => option.toLowerCase().includes(filterValue));
  }

}
