import { Component, OnInit, ViewChild, Inject, AfterViewChecked } from '@angular/core';
import { DataService } from '../data.service';
import { MatTableDataSource } from '@angular/material';
import {MatPaginator, MatSort} from '@angular/material';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';
import { OrderDialogComponent } from '../order-dialog/order-dialog.component';
import { OrdersService } from '../orders.service';
import { element } from '@angular/core/src/render3';
import {MatDatepickerInputEvent} from '@angular/material/datepicker';
import {FormControl} from '@angular/forms';
declare var $ :any;


export interface OrderView {
  orderId: string;
  amount: string;
  date: string;
  deliveryBy: string;
  items: any[];
}

export interface PostData {
  orderId: number;
  date: string;
  amount: string;
  deliveryBy: string;
  items: any[];
}
let dateFrom: string;
@Component({
  selector: 'app-orders',
  templateUrl: './orders.component.html',
  styleUrls: ['./orders.component.css']
})
export class OrdersComponent implements OnInit , AfterViewChecked{

  orderId: string;
  amount: string;
  date: string;
  deliveryBy: string;
  items: any[];
  postdata: PostData[];

  
  today: any = Date.now();
  orders:any;
  displayedColumns: string[] = ['position', 'name', 'amount','delivery', 'action'];
  ordersData : Object[] = [];
  dataSource = new MatTableDataSource(this.ordersData);
  length = 100;
  pageSize = 10;
  pageSizeOptions: number[] = [5, 10, 25, 100];

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  dateFrom = new FormControl(new Date());
  dateTo = new FormControl(new Date());
  constructor(public orderService: OrdersService, public dataService: DataService, public dialog: MatDialog) { }

  ngAfterViewChecked(){
    
      //this.totalOrders();
      // this.sumTotal();
  }

  ngOnInit() {
    
    var data: any = [];
    var sum: any = 10;
    this.orders = [];
    this.postdata = [];
    this.orderService.getOrders().subscribe((data: PostData[]) => {
      this.postdata = data;
    console.log(data);
      this.dataSource.data = data;
      console.log(this.dataSource.data);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
      this.dataSource.data.forEach(element => {
        sum += parseInt(element['amount']);
      })
      console.log(sum);
      $('.total-orders-amount').html(sum);
      this.totalOrders();
      console.log();
    });
    
  }

  deleteOrder(orderId){
    this.orderService.deleteOrder(orderId).subscribe((data: PostData[]) => {
      this.orderService.getOrders().subscribe((data: PostData[]) => {
        this.dataSource.data = data;
      })
    })
  }

  getCustomDate(type: string, event: MatDatepickerInputEvent<Date>) {
    var sum: any = 10;
    var dateFrom = this.dateFrom.value.getDate() + "/"
                  + (this.dateFrom.value.getMonth()+1)  + "/" 
                  + this.dateFrom.value.getFullYear();
    
    var dateTo = this.dateTo.value.getDate() + "/"
                  + (this.dateTo.value.getMonth()+1)  + "/" 
                  + this.dateTo.value.getFullYear();
    console.log(dateFrom+ ' - ' +dateTo);
    this.orderService.getCustomOrders(dateFrom,dateTo).subscribe((data: PostData[]) => {
      this.postdata = data;
      console.log(data);
      this.dataSource.data = data;
      console.log(this.dataSource.data);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
      this.dataSource.data.forEach(element => {
        sum += parseInt(element['amount']);
      })
      $('.total-orders-amount').html(sum);
      this.totalOrders();
      console.log();
    });
  }

  openDialog(orderId): void {
    this.orderService.getOrderById(orderId).subscribe((data: {}) => {
      this.orders = data;
      console.log(this.orders);
      this.dialog.open(OrderDialogComponent, {
        width: '450px',
        data: {orderId: orderId, details: this.orders}
      });
    });
  }

  applyFilter(filterValue: string) {
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  totalOrders(){
    var orders: any = 0;
    $(".orders-table tbody tr ").each(function() {
    orders += 1;
          })
    $('.total-orders').html(orders);
  }

}
