@Injectable({
  providedIn: 'root'
})
export class DataService {
  displayedColumns: string[] = ['position', 'name', 'email' , 'amount', 'quantity', 'columndelete'];
  dataSource: MatTableDataSource<any>;
  result:any;
  //myControl = new FormControl();
  options: string[] = ['Straight Cut Fries-R', 'Straight Cut Fries-M', 'Straight Cut Fries-L', 
                        'Tangled Tango', 'Eldorado Buzz', 'Cloud Green','Winky Twister','Mayo-Li', 'Bar-b-q Pizza Fries',
                        'Cream Chicken Pizza Fires', 'Chicken Fajita Pizza Fries', 'Supreme Pizza Fries',
                        'Nuggets (8 pcs)','Wings (8 pcs)','345ml', '500ml', '1.5 Ltr', 'Aqua Fina 500ml' 
                      ];
  filteredOptions: Observable<string[]>;
  selectedtableData: PeriodicElement[] = [];
  constructor(private _http: HttpClient) { }

  getItems(){
    return this._http.get('assets/items.json');
  }

  private extractData(res: Response) {
    let body = res;
    return body || { };
  }

  

  getOrderById(orderId): Observable<any> {
    // return this._http.get('/api/getorderById').pipe(
    //   map(this.extractData));
    return this._http.get(endpoint +'/api/getorderById', {params: {orderId: orderId}}).pipe(
        map(this.extractData));
    // this._http.post('/api/insertorders',{
    //   "orderId": orderId
    // }).subscribe(
    //   data => {
    //       console.log("POST Request is successful ", data);
          
    //   },
    //   error => {
    //       console.log("Error", error);
    //   }
      
    // );  
    // return;
  }

  getProducts(): Observable<any> {
    return this._http.get(endpoint +'/api/orders').pipe(
      map(this.extractData));
  }

  getOrders(): Observable<any> {
    return this._http.get(endpoint +'/api/getorders').pipe(
      map(this.extractData));
  }

  insertOrder(orderId, items, orderDate, deliverBy): void{
    var total: any;
    total = 0;
    items.data.forEach(element => {
      total = total + element.amount;
    })
    this._http.post(endpoint +'/api/insertorders',{
      "orderId": orderId,
      "amount": total,
      "orderDate": orderDate,
      "deliveryBy": deliverBy,
      "items": [items.data]
    }).subscribe(
      data => {
          console.log("POST Request is successful ", data);
      },
      error => {
          console.log("Error", error);
      }
    );  
  }

  addOrder(){
    // console.log(this.selectedtableData);
    var jsonObj : any = [];
    var jsonQnty : any = [];
    $('.table-item tbody tr').each(function (){
      var checkbox = $(this).find('.mat-checkbox-input').attr('aria-checked');
      
        if(checkbox == 'true'){
            const selectedValue = $(this).find('.tr-item-name').text();
            const qunty = $(this).find('.quantity-box').val();
            let selectedvalueArr: any = ELEMENT_DATA.find(e=>e.name==selectedValue);
            selectedvalueArr.quantity = qunty;
            selectedvalueArr.amount = qunty * selectedvalueArr.price;
            // console.log(selectedvalueArr);
            jsonObj.push(selectedvalueArr);
            jsonQnty.push(qunty);
        }
      
      })
      jsonObj.forEach(element => {
        //console.log(element);
        this.selectedtableData.push(element)
      });   
      jsonQnty.forEach(element => {
        this.selectedtableData
      });
     this.dataSource = new MatTableDataSource(this.selectedtableData);
     this.dataSource._updateChangeSubscription();

    //  console.log();

  }

  deleteItem(elm){
    let ind : any = this.selectedtableData.indexOf(elm);
  this.selectedtableData.splice(ind, 1);
  this.dataSource = new MatTableDataSource(this.selectedtableData);
  this.dataSource._updateChangeSubscription();
  }

  clearTable(){
    let empty : any = null;
    this.selectedtableData = [];
    this.dataSource = new MatTableDataSource(empty);
    this.dataSource._updateChangeSubscription();
  }

}
