import { Component, OnInit } from '@angular/core';
import {OrdersService} from '../orders.service';

export interface PostData {
  orderId: number;
  date: string;
  amount: string;
  deliveryBy: string;
  items: any[];
}

@Component({
  selector: 'app-sale-end',
  templateUrl: './sale-end.component.html',
  styleUrls: ['./sale-end.component.css']
})
export class SaleEndComponent implements OnInit {
totalAmount :any;
totalOrders: any;
postdata: PostData[];
saleEnded: any;
sales: any[];
  constructor(public orderService: OrdersService) { }

  ngOnInit() {
    this.orderService.endSale().subscribe((data: PostData[]) => {
      console.log(data);
      this.postdata = data;
      this.totalAmount = 0;
      this.totalOrders = 0;
      this.postdata.forEach(element => {
        this.totalAmount += parseInt(element['amount']);
        this.totalOrders += 1;
      })
    });
  }

  vanishSale(){
    this.orderService.vanishSale().subscribe((data: {})=>{
        this.saleEnded = "You Sale is now ended."
    });
  }

}
