import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import {Observable} from 'rxjs';
import { HttpHeaders } from '@angular/common/http';
import {map} from 'rxjs/operators';

const httpOptions = {
  headers: new HttpHeaders({
    'Content-Type':  'application/json',
    'Authorization': 'my-auth-token'
  })
};

@Injectable({
  providedIn: 'root'
})
export class OrdersService {

  
  uri = 'https://fries-factory.herokuapp.com/business';
  constructor(private http: HttpClient) { }

  private extractData(res: Response) {
    let body = res;
    return body || { };
  }

  getOrders(): Observable<any> {
    var date = new Date();
    var currentDate = date.getDate() + "/"
                    + (date.getMonth()+1)  + "/" 
                    + date.getFullYear();
    return this.http.get(`${this.uri}`+'?orderDate='+ currentDate).pipe(
      map(this.extractData));
  }

  getOrderById(orderId): Observable<any> {
    return this.http.get(`${this.uri}/getOrderById`+'?orderId='+ orderId).pipe(
      map(this.extractData));
  }

  endSale(): Observable<any>{
    return this.http.get(`${this.uri}/endSale`).pipe(
      map(this.extractData));
  }

  vanishSale(): Observable<any>{
    return this.http.get(`${this.uri}/vanishSale`).pipe(
      map(this.extractData));
  }

  deleteOrder(orderId): Observable<any>{
    return this.http.get(`${this.uri}/deleteOrder`+'?orderId=' +orderId).pipe(
      map(this.extractData)
    );
  }

  getCustomOrders(dateFrom, dateTo): Observable<any> {
    var date = new Date();
    var currentDate = date.getDate() + "/"
                    + (date.getMonth()+1)  + "/" 
                    + date.getFullYear();
    return this.http.get(`${this.uri}/customDate`+'?dateFrom='+dateFrom+'&'+'dateTo='+dateTo).pipe(
      map(this.extractData));
  }

  addOrder(amount, items, deliveryBy) {
    var date = new Date();
    var currentDate = date.getDate() + "/"
                    + (date.getMonth()+1)  + "/" 
                    + date.getFullYear();
    var orderId: any;
    orderId = date.getDate() + date.getMonth() + date.getFullYear() + date.getMinutes() + date.getHours() + date.getSeconds();                
    const obj = {
      amount: amount,
      order_date: currentDate,
      orderId: orderId,
      deliveryBy: deliveryBy,
      items: items
    };
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    console.log(obj);
    this.http.post(`${this.uri}/add`, obj, httpOptions)
        .subscribe(res => console.log('Done'));
  }

}
