import { Component, Inject , OnInit} from '@angular/core';
import { DataService } from '../data.service';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';
import {ItemDialogComponent} from '../item-dialog/item-dialog.component';
import {OrdersService} from '../orders.service';


export interface DialogData {
  itemName: string;
  data: string;
  name: string;
}

export interface PostData {
  orderId: number;
  date: string;
  amount: string;
  deliveryBy: string;
}



import { Observable } from 'rxjs';

@Component({
  selector: 'app-items',
  templateUrl: './items.component.html',
  styleUrls: ['./items.component.scss']
})
export class ItemsComponent  {

  items$: Object;
  itemName: string;
  itemDetails: string;
  postdata: PostData[];
  constructor(private data: DataService,private orders: OrdersService,public dialog: MatDialog) { }

  ngOnInit(){
    this.data.getItems().subscribe(
      data => this.items$ = data 
    )
  }

  // addBusiness(amount, order_date, orderid, deliveryBy) {
  //   this.orders.addBusiness(amount, order_date, orderid, deliveryBy);
  // }

  getBusiness() {
    this.orders.getOrders().subscribe((data: PostData[]) => {
      this.postdata = data;
      console.log(this.postdata);
    });
  }

  openDialog(item): void {
    const dialogRef = this.dialog.open(ItemDialogComponent, {
      width: '50%',
      data: {item:item, details:item.details}
    });

    dialogRef.afterClosed().subscribe(result => {
      // console.log('The dialog was closed');
      // this.itemName = result;
      // console.log(result);
    });
  }

}





